.container-contacts {
  background-color: #fff5f6;

   @media screen and (min-width: 1200px) {
  background-image: url(../images/7-contacts/contacts.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
   }
}


.contacts {
   display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: baseline;
  margin-left: auto;
  margin-right: auto;

   @media screen and (min-width: 768px) {
     margin: 0;
     justify-content: space-between;
  } 
     @media screen and (min-width: 1200px) {
     margin: 0;
     justify-content: center;
  } 
}

.contacts-cards {
    list-style: none;
  padding: 43px 20px;
  max-width: 220px;
  width: 100%;

  background-color: var(--white);
  border-radius: 24px;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);

  &:not(:last-child) {
    margin-bottom: 20px;
  }
      @media screen and (min-width: 1200px) {
       max-width: 300px;
       width: 100%;

        &:not(:last-child) {
        margin-right: 31px;
        margin-bottom: 0;
  } 
      }
}

.contacts-tittle {
  display: inline-block;
  padding: 3px 24px;
  min-width: 80px;

  margin-bottom: 25px;
  text-transform: uppercase;
  font-weight: 500;
  line-height: 1.5;

  background-color: #f0d1a5;
  color: var(--white);
  border-radius: 4px;
  list-style: none;

     @media screen and (min-width: 1200px) {
       margin-bottom: 32px;
     }
}
.food {
  background-color: #c2e297;
}

.contacts-town {
  color: var(--black);
  margin-bottom: 10px;
  font-weight: 500;
  line-height: 1.5;
}

.contacts-about {
  display: block;
  margin-bottom: 25px;

  font-weight: 500;
  line-height: 1.5;
  color: var(--grey);

       @media screen and (min-width: 1200px) {
       margin-bottom: 32px;
     }
}

.contacts-work-list {
  margin-top: 15px;
  margin-bottom: 25px;

  font-weight: 500;
  line-height: 1.5;
  border-top: 1px solid #e1e1e1;
  border-bottom: 1px solid #e1e1e1;
    list-style: none;

     @media screen and (min-width: 1200px) {
        padding-top: 22px;
        padding-bottom: 32px;
     }
}

.contacts-work-days {
  color: var(--black);
  font-weight: 500;
  line-height: 1.5;
  margin-top: 10px;
    list-style: none;

         @media screen and (min-width: 1200px) {
        margin-top: 15px;
     }
}

.contacts-work-time {
  display: block;
  font-weight: 500;
  line-height: 1.5;
  color: var(--pink);
  list-style: none;
}

.contscts-nav {
  margin-top: 25px;
  list-style: none;

  @media screen and (min-width: 1200px) {
        margin-top: 32px;
     }
}

.contacts-item {
  font-weight: 500;
  line-height: 1.5;
  color: var(--grey);
}

.contact-list {
  display: flex;  
  justify-content: center;
  flex-direction: column;

     @media screen and (min-width: 768px) {
     flex-direction: row;
  } 
}

.contacts-button-list {
  margin-bottom: 10px;
  list-style: none;

  &:last-child {
    margin-bottom: 0;
  }
     @media screen and (min-width: 768px) {
     margin-right: 15px;
     margin-bottom: 0;

      &:last-child {
      margin-right: 0;
  }
  } 

}


.contacts-buttons {
  padding-top: 54px;
  padding-bottom: 113px;

   @media screen and (min-width: 768px) {
     padding-bottom: 158px;
   }

    @media screen and (min-width: 1200px) {
     padding-bottom: 165px;
   }
}

.contact-button-white {
  display: flex;
  justify-content: center;
  padding: 10px 32px;
  margin-left: auto;
  margin-right: auto;

  background: var(--white);
  border-radius: 22px;
  border: 1px solid var(--white);
  min-width: 160px;
  color: var(--dark-pink);
  font-weight: bold;
  font-size: 16px;
  line-height: 1.312;

  &hover,
  &fucus {
    background-color: var(--dark-pink);
    border: 1px solid var(--dark-pink);
    color: var(--white);
  }
}

.contact-button-pink {
  display: flex;
  justify-content: center;
  padding: 10px 32px;
  margin-left: auto;
  margin-right: auto;

  background: var(--dark-pink);
  border-radius: 22px;
  border: 1px solid var(--dark-pink);
  min-width: 160px;
  color: var(--white);
  font-weight: bold;
  font-size: 16px;
  line-height: 1.312;

  &hover,
  &fucus {
    background-color: var(--white);
    border: 1px solid var(--white);
    color: var(--dark-pink);
  }
}


